






















































export default {
	data() {
		return {
			list:''
		};
	},
	onLoad(){
		
	},
	methods:{
		share(){
			uni.getProvider({
				service: 'share',
				success: function (res) {
					console.log(JSON.stringify(res.provider))
					if (~res.provider.indexOf('weixin')) {
						uni.share({
							provider: "weixin",
							scene: "WXSceneSession",
							type: 1,
							summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
							success: function (res) {
								console.log("success:" + JSON.stringify(res));
							},
							fail: function (err) {
								console.log("fail:" + JSON.stringify(err));
							}
						});
					}
				}
			});
		}
	},
	onShow(){
		uni.showLoading({
			title:'加载中...'
		})
		uni.request({
			url:this.http+'user/getIntegralList',
			header:this.header,
			method:'POST',
			data:{
				user_id:uni.getStorageSync('token')
			},
			success:(res)=>{
				uni.hideLoading()
				for(var i in res.data.data.list){
					var str = new Date(parseInt(res.data.data.list[i].add_time) * 1000).toLocaleString().replace(/:\d{1,2}$/,' ');
					res.data.data.list[i].add_time = str.substr(0, str.indexOf(' '))
				}
				this.list = res.data.data
			},
			fail(){
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	}
}
